(function() {
  'use strict';

  angular
    .module('websnap')
    .directive('chatbox', chatbox);

  /** @ngInject */
  function chatbox() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/chatbox/chatbox.html',
      scope: {
          creationDate: '='
      },
      controller: ChatBoxController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function ChatBoxController($mdDialog, $document, ChatService) {
      var vm = this;
      vm.options = ChatService.getChats();

        vm.expand = function(from) {
          $mdDialog.show({
            controller: function($mdDialog, $scope, $interval, ChatService) {
              $scope.from = from;
              $scope.index = 0;
              $scope.chats = ChatService.getChatsByName(from).content;
              $scope.progress = 0;
              var msgRead = 1;
              
              $scope.cancel = function() {
               for (var i = $scope.chats.length - 1; i >= 0; i--) {
                 $scope.chats[i].progress = 0;
               }
               if (angular.isDefined(promise)) {
                  $interval.cancel(promise);
                  promise = undefined;
                }
               ChatService.deleteChatByIndex(from, msgRead);
               $scope.index = 0;
               msgRead++;
               $mdDialog.cancel();
              };

              $scope.next = function() {
               $scope.chats[$scope.index].progress = 100;
               $scope.index++;
               if ($scope.index == $scope.chats.length)
                    $scope.cancel();
               else
                    msgRead++;
              };

              var promise = $interval(function() {
                $scope.chats[$scope.index].progress += 0.67;
                  if ($scope.chats[$scope.index].progress >= 100)
                    $scope.next();
              }, 100, $scope.chats.length*150, true);
            },
            templateUrl: 'app/components/chatbox/viewModal.html',
            parent: angular.element($document.body),
            clickOutsideToClose:false,
            fullscreen: true
          })
        };
      }
    }
})();

'use strict';

angular
  .module('websnap')
  .factory('SocketFactory', function($rootScope) {
    var url = 'http://localhost:3005';

    var socket = io.connect(url);
    return {
      id: 0,
      on: function(eventName, callback) {
        socket.on(eventName, function() {
          var args = arguments;
          $rootScope.$apply(function() {
            callback.apply(socket, args);
          });
        });
      },
      emit: function(eventName, data, callback) {
        socket.emit(eventName, data, function() {
          var args = arguments;
          $rootScope.$apply(function() {
            if (callback) {
              callback.apply(socket, args);
            }
          });
        });
      }
    };
  });

(function() {
  'use strict';

  angular
    .module('websnap')
    .service('ImageService', function(){
      this.processImage = function(file,canvasRef) {
          canvas = canvasRef;
          ctx = canvas.getContext("2d");
          var reader = new FileReader();
          reader.readAsDataURL(file);
          reader.onload = function() {
              image.src = reader.result;
              canvas.height = 450;
              canvas.width = 570;
          }
      };

      this.getDataURL = function() {
        var temp = dataURL;
        dataURL = "";
        return temp;
      }

      this.resetData = function() {
        dataURL = "";
        canvas = null;
        ctx = null;
      }

      var onLoadHandler = function() {
        // if (image.width < image.height){
        //   console.log("Portait", image.height, image.width);
        //   var ratio = image.height/image.width;
        //   var height = canvas.height;
        //   var width = height*ratio;
        //   var offset = (canvas.width - width)/2;
        //   ctx.drawImage(image, offset, 0, width, height);
        // }
        // else{
        //   console.log("canvas", canvas.width, canvas.height);
        //   console.log("Landscape", image.width, image.height);
        //   var ratio = image.width/image.height;
        //   var width = canvas.width;
        //   var height = width*ratio;
        //   console.log(width, height);
        //   var offset = (canvas.height - height)/2;
        //   ctx.drawImage(image, 0, offset, width, height);
        //}
          ctx.drawImage(image, 0, 0, canvas.width, canvas.height);
          dataURL = canvas.toDataURL();
      };

      var canvas,ctx;
      var image = document.createElement("img");
      image.crossOrigin = "Anonymous";
      image.onload = onLoadHandler;

      var dataURL = "";
     
    });

})();


(function() {
  'use strict';

  angular
    .module('websnap')
    .service('ContactService', function($q){
        //User info
        var username = "",
        firstname = "",
        lastname = "",
        userId = "",
        loggedIn = false;

        var contacts = [];

        this.authenticate  = function() {
          if(loggedIn){
                //If authenticated, return anything you want, probably a user object
                return true;
            } else {
                //Else send a rejection
                return $q.reject('Not Authenticated');
            }
        };

        this.resetUserInfo = function() {
          username = "";
          firstname = "";
          lastname = "";
          userId = "";
          loggedIn = false;
        };

        this.registerUserInfo = function(obj) {
          username = obj.username;
          firstname = obj.fname;
          lastname = obj.lname;
          userId = obj.objectId;
          loggedIn = true;
        };

        this.importContacts = function(array) {
          contacts = array;
        };

        this.getUserName = function() {
          return username;
        };

        this.getFirstName = function() {
          return firstname;
        };

        this.getLastName = function() {
          return lastname;
        };

        this.getUserId = function() {
          return userId;
        };

        this.getContacts = function() {
          return contacts.map(function (c) {
              var contact = {
                name: c.username,
                userId: c.objectId,
                _lowername: c.username.toLowerCase()
              };
              return contact;
            });
        };
     
    });

})();


(function() {
  'use strict';

  angular
    .module('websnap')
    .service('ChatService', function(ApiService){
      //Data Structure
      // {
      //     from: "Mike",
      //     content: [
      //     {
      //       msg: "Mike said Hey what's up 1",
      //       time: "10/17/2016",
      //       progress: 0
      //     },
      //     {
      //       msg: "Mike said Hey what's up 2",
      //       time: "10/16/2016",
      //       progress: 0
      //     },
      //     {
      //       msg: "Mike said Hey what's up 3",
      //       time: "10/15/2016",
      //       progress: 0
      //     }
      //     ]
      //   }
      var chats = [];

      this.addChat = function(from, msg, time, chatId) {
        var exist = false;
        var chatExist = false;
        var imageURL = "http://localhost/api/resource/"+chatId;
        for (var i = chats.length - 1; i >= 0; i--) {
          if (chats[i].from == from){
            exist = true;
            for (var j = chats[i].content - 1; j >= 0; j--) {
              if (chats[i].content[j].chatId == chatId)
                chatExist = true;
            }
            if (!chatExist){
            chats[i].content.unshift({
              msg: msg,
              chatId: chatId,
              time: time,
              url: imageURL,
              progress: 0
            });
            }
          }
        }
        if (!exist)
          chats.push({
              from: from,
              content: 
              [
                {
                  msg: msg,
                  chatId: chatId,
                  time: time,
                  url: imageURL,
                  progress: 0
                }
              ]
          })
      };

      this.deleteChatByIndex = function(name, index) {
        for (var i = chats.length - 1; i >= 0; i--) {
          if (chats[i].from == name)
            {
              for (var j = index - 1; j >= 0; j--) {
                ApiService.removeChat(chats[i].content[j].chatId);
              }
              chats[i].content.splice(0, index);

              if (chats[i].content.length == 0)
                 chats.splice(i, 1);
            }
        }
      };

      this.getChatsByName = function(name) {
        for (var i = chats.length - 1; i >= 0; i--) {
          if (chats[i].from == name)
            return chats[i]
        }
      };

      this.getChats = function() {
        return chats;
      };
     
    });

})();


(function() {
  'use strict';

  angular
    .module('websnap')
    .service('ApiService', function($http, $log, ContactService){
        var apiBase = "http://localhost/api/"
        var generateRoute = function(route) {
          return apiBase+route;
        }
        var httpSuccess = function(msg) {
          $log.info('API call success: ' + msg);
        };

        var httpError = function(msg) {
          $log.info('API call error: ' + msg + '\nAPI unavailable\nPlease check the network connection');
        };

         this.signup = function(signUpForm, sCallback, eCallback) {
          var apiCall = generateRoute('signup');
          return $http.post(apiCall, signUpForm).then(
            function success(response) {
              var msg = 'Sign Up Sucsess!';
              httpSuccess(msg);
              sCallback(response);
            },
            function error(response) {
              var msg = 'Unable to Sign Up...';
              httpError(msg);
              eCallback(response);
            }
          );
        };

       this.login = function(loginForm, sCallback, eCallback) {
        var apiCall = generateRoute('login');
        return $http.post(apiCall, loginForm).then(
          function success(response) {
            var msg = 'Log in Sucsess!';
            httpSuccess(msg);
            sCallback(response);
          },
          function error(response) {
            var msg = 'Unable to Log in...';
            httpError(msg);
            eCallback(response);
          }
        );
      };

       this.getquery = function(query, sCallback) {
        var apiCall = generateRoute('getquery');
        return $http.post(apiCall, {
          query: query
        }).then(
          function success(response) {
            sCallback(response);
          },
          function error() {
            var msg = 'Unable to retrive query data...';
            httpError(msg);
          }
        );
      };

      this.sendmsg = function(tos, msg, imageUrl) {
        var apiCall = generateRoute('sendmsg');
        return $http.post(apiCall, {
          from: ContactService.getUserName(),
          tos: tos,
          msg: msg,
          url: imageUrl
        }).then(
          function success() {
            var msg = 'Message sent!';
            httpSuccess(msg);
          },
          function error(response) {
            console.log(response);
            var msg = 'Unable to send message...';
            httpError(msg);
          }
        );
      };

      this.getUnreadMessage = function(sCallback) {
        var apiCall = generateRoute('querydata');
        return $http.post(apiCall, {
          query: ContactService.getUserId(),
        }).then(
          function success(response) {
            var msg = 'Got all the unread messages';
            httpSuccess(msg);
            sCallback(response);
          },
          function error(response) {
            var msg = 'Unable to retrive unread message...';
            httpError(msg);
          }
        );
      };

      this.removeChat = function(chatId) {
        var apiCall = generateRoute('removechat');
        return $http.post(apiCall, {
          objectId: chatId,
        }).then(
          function success() {
            var msg = 'Deleted message '+chatId;
            httpSuccess(msg);
          },
          function error() {
            var msg = 'Unable to retrive unread message...';
            httpError(msg);
          }
        );
      };

      });
})();

(function() {
  'use strict';

  angular
    .module('websnap')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController(SocketFactory, ContactService, ChatService, ApiService) {
    var vm = this;
    
    var sucessHandler = function(res) {
      if (res.data.length > 0)
        for (var i = res.data.length - 1; i >= 0; i--) {
          ChatService.addChat(res.data[i].from, res.data[i].msg, res.data[i].time, res.data[i].objectId);
        }
    }

    if(ContactService.getUserId() != ""){
      ApiService.getUnreadMessage(sucessHandler);
      console.log("Listening WebSocket");
    	SocketFactory.on(ContactService.getUserId(), function(data) {
        ChatService.addChat(data.from, data.msg, data.time, data.chatId);
      })
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('websnap')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController($location, $scope, ApiService, ContactService) {
    var vm = this;
    vm.toggle = false;
    vm.error = false;
    vm.errMsg = "";

    var sucessHandler = function(res) {
      ContactService.registerUserInfo(res.data);
      $location.path('inbox');
    };
    var errorHandler = function(res) {
      vm.errMsg = res.data.message || "Server is down at the moment...";
      vm.error = true;
    }
    vm.resetError = function() {
      vm.error = false;
      vm.errMsg = "";
    }
    vm.login = function() {
      if($scope.loginForm.$valid)
        {
          var form = {
            username: vm.username,
            password: vm.password
          }
          ApiService.login(form, sucessHandler, errorHandler);
        }
      else
        console.log("Not valid");
    }
    vm.signup = function() {
      if($scope.signupForm.$valid)
        {
          var form = {
            username: vm.rUsername,
            password: vm.rPassword,
            fname: vm.fname,
            lname: vm.lname
          }
          ApiService.signup(form, sucessHandler, errorHandler);
        }
      else
        console.log("Not valid");
    }
    vm.toggleSignUp = function() {
      vm.toggle = !vm.toggle;
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('websnap')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $rootScope, $location) {
  	$rootScope.$on('$routeChangeError', function(event, current, previous, rejection)
  	 {
        if(rejection === 'Not Authenticated'){
            $location.path('/login');
        }
      })
  }

})();

(function() {
  'use strict';

  angular
    .module('websnap')
    .config(routeConfig);

  function routeConfig($routeProvider, $locationProvider) {
    $routeProvider
      .when('/inbox', {
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'vm',
         resolve: {
            'auth' : function(ContactService){
                return ContactService.authenticate();
            }
        }
      })
      .when('/login', {
        templateUrl: 'app/login/login.html',
        controller: 'LoginController',
        controllerAs: 'vm'
      })
      .otherwise({
        redirectTo: '/login'
      });

    $locationProvider.html5Mode({
       enabled: true,
       requireBase: false
      });
  }

})();

(function() {
  'use strict';

  angular
    .module('websnap')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

